import React from 'react'
import { useState, useRef, useEffect } from 'react'
import SvgCmp from '../../../../components/svgComponent/SvgCmp'

interface ActiveIds {
	[key: string]: boolean
}

const FAQItem = (props: any) => {
	const contentRef = useRef<HTMLDivElement>(null)
	const [maxHeight, setMaxHeight] = useState('0px')
	const [isRotated, setIsRotated] = useState(false)
	const [activeIds, setActiveIds] = useState<ActiveIds>({
		accordion1: false,
		accordion2: false,
		accordion3: false,
		accordion4: false,
	})

	const handleClick = (id: string) => {
		setActiveIds(prev => ({
			...prev,
			[id]: !prev[id],
		}))
		setIsRotated(!isRotated)
	}
	useEffect(() => {
		setMaxHeight(activeIds[props.item.id] ? `${contentRef.current?.scrollHeight}px` : '0px')
	}, [activeIds, props.item.id])

	const titleClasses =`flex items-center justify-between w-full p-4 text-base font-semibold rtl:text-right ${activeIds[props.item.id] ? 'bg-faq-click rounded-t-2xl' : 'hover:bg-faq-hover rounded-2xl'} `
	const contentClasses = `px-4 bg-faq-click rounded-b-2xl pb-4 mb-2 rtl:text-right`


	return (
		<React.Fragment>
			<h2 id='accordion-color-heading-1' className='mb-2'>
				<button
					type='button'
					onClick={() => handleClick(props.item.id)}
					className={`${titleClasses} transition-all duration-300 ease-in`}
					data-accordion-target='#accordion-color-body-1'
					aria-expanded='true'
					aria-controls='accordion-color-body-1'>
					<span>{props.item.title}</span>
					<SvgCmp svgName='plus' type='faq' styles={`${isRotated ? 'transform rotate-45' : ''} transition-transform duration-500`}/>
				</button>
				<div
				ref={contentRef}
				style={{ maxHeight }}
					id={`accordion-color-body-${props.item.num}`}
					className={`overflow-hidden transition-max-height duration-300 ease-in`}
					aria-labelledby='accordion-color-heading-1'>
					<div className={`${contentClasses}`}>
						<p className='mb-2 xl:text-sm xl:font-normal leading-normal'>{props.item.content}</p>
					</div>
				</div>
			</h2>
		</React.Fragment>
	)
}

export default FAQItem
