const CheckImg = (param: any) => {
	switch (param) {
		case 1:
			return 'bg-[url("/gallery/gallery-1.jpeg")]'
		case 2:
			return 'bg-[url("/gallery/gallery-2.jpeg")]'
		case 3:
			return 'bg-[url("/gallery/gallery-3.jpeg")]'
		case 4:
			return 'bg-[url("/gallery/gallery-4.jpeg")]'
		case 5:
			return 'bg-[url("/gallery/gallery-5.jpeg")]'
		default:
			return 'error'
	}
}
const MobileGalleryItem = (props: any) => {
	const image: string = CheckImg(props.img)

	return (
		<div className='relative inline-block m-2 bg-slate-500 rounded-xl overflow-hidden'>
			<div className={`h-[7.5rem] w-[7.5rem] ${image} bg-center`}>
			</div>
		</div>
	)
}

export default MobileGalleryItem
