import { useEffect, useState } from 'react'
import { Link } from 'react-router-dom'
import { useNavigate } from 'react-router-dom'
import { useDispatch, useSelector } from 'react-redux'
import { heroActions } from '../../store/hero-slice'
import SvgCmp from '../svgComponent/SvgCmp'

const getWindowSize = () => {
	const { innerWidth, innerHeight } = window
	return { innerWidth, innerHeight }
}

const SecondaryButton = () => {
	const dispatch = useDispatch()
	const heroState = useSelector((state: any) => state.hero)
	const nav = useSelector((state: any) => state.hero.nav)

	const navigate = useNavigate()
	const handleClickRoof = () => {
		if (heroState.isRoofsActive !== true) {
			dispatch(heroActions.clickRoofs(true))
			dispatch(heroActions.clickPlants(false))
			navigate(nav)
			window.scrollTo({ top: 0, behavior: 'smooth' })
		}
	}
	const [windowSize, setWindowSize] = useState(getWindowSize())

	useEffect(() => {
		function handleWindowResize() {
			setWindowSize(getWindowSize())
		}

		window.addEventListener('resize', handleWindowResize)

		return () => {
			window.removeEventListener('resize', handleWindowResize)
		}
	}, [])

	return (
		<Link
			to='/ZZiR/ZieloneDachy'
			type='button'
			className={`text-green-700 bg-none border border-green-700 focus:outline-none hover:bg-gray-300/10 text-base font-semibold rounded-lg px-4 pr-[12px] xl:px-4 py-[6px] xl:py-[6px] flex flex-row`}
			onClick={handleClickRoof}>
			<SvgCmp
				svgName='home'
				type='button'
			/>
			{/* <span className={windowSize.innerWidth > 768 ? 'btn-svg' : 'btn-svg--mobile'}></span> */}
			<p className=''>Zielone dachy</p>
		</Link>
	)
}

export default SecondaryButton
