// let isMobile
const Offers = () => {
	const [activeItem, setActiveItem] = useState<any>(offerList[0])
	// const [clicked, setClicked] = useState(false)

	const handleCardClick = (event: any) => {
		setActiveItem(offerList.find(x => x.id === event.currentTarget.id))
	}

	const contentOfferList = offerList.map(offer => (
		<OfferItem
			key={offer.id}
			id={offer.id}
			img={offer.img}
			title={offer.title}
			text={offer.text}
			background={offer.background}
			customClickEvent={handleCardClick}
			isActive={activeItem.id === offer.id}
		/>
	))

	const contentOfferMobileList = offerList.map(offer => (
		<OfferMobileItem
			key={offer.id}
			id={offer.id}
			img={offer.img}
			title={offer.title}
			text={offer.text}
			background={offer.background}
			customClickEvent={handleCardClick}
			isActive={activeItem.id === offer.id}
		/>
	))

	const mobile = (
		<div className='mt-6 bg-green-900'>
			<div className='max-md:block max-md:w-full max-md:overflow-hidden pt-8'>
				<div className='max-md:block w-full overflow-x-scroll whitespace-nowrap h-52'>{contentOfferMobileList}</div>
			</div>
			<div className='col-span-4'>
				<OfferInfo
					key={activeItem.id}
					isActive={activeItem.page}
					itemsAmount={offerList.length}
					title={activeItem.title}
					text={activeItem.text}
				/>
			</div>
		</div>
	)

	const desktop = (
		<div className='xl:mt-12 bg-green-900 xl:rounded-2xl'>
			<div className='xl:p-12 xl:grid xl:grid-cols-12'>
				<div className='xl:col-span-8 xl:grid xl:grid-cols-4 xl:gap-4'>{contentOfferList}</div>
				<div className='col-span-4'>
					<OfferInfo
						key={activeItem.id}
						isActive={activeItem.page}
						itemsAmount={offerList.length}
						title={activeItem.title}
						text={activeItem.text}
						length={offerList.length}
					/>
				</div>
			</div>
		</div>
	)

	return <>{window.innerWidth < 768 ? mobile : desktop}</>
}

export default Offers
