import React, { useEffect, useState, useRef } from 'react'

let status = true
const Stat = (props: any) => {
	const { title, number, duration, prefix } = props.data
	const [count, setCount] = useState('0')
	const [isVisible, setIsVisible] = useState(false)
	const ref = useRef<HTMLDivElement>(null)

	//#region Is Visible
	useEffect(() => {
		const observer = new IntersectionObserver(
			([entry]) => {
				// Update our state when observer callback fires
				setIsVisible(entry.isIntersecting)
			},
			{
				root: null,
				rootMargin: '0px',
				threshold: 0.1,
			}
		)
		if (ref.current) {
			observer.observe(ref.current)
		}
		return () => {
			if (ref.current) {
				observer.unobserve(ref.current)
			}
		}
	}, [])
	//#endregion

	//#region Counting
	// number displayed by component
	useEffect(() => {
		if (isVisible) {
			let start = 0
			// first three numbers from props
			const end = parseInt(number.substring(0, 3))
			// if zero, return
			if (start === end) return

			// find duration per increment
			let totalMilSecDur = parseInt(duration)
			let incrementTime = (totalMilSecDur / end) * 1000

			// timer increments start counter
			// then updates count
			// ends if start reaches end
			let timer = setInterval(() => {
				start += 1
				setCount(String(start) + number.substring(3))
				if (start === end) clearInterval(timer)
			}, incrementTime)
		}

		// dependency array
	}, [number, duration, isVisible])

	//#endregion
	return (
		<div
			ref={ref}
			className='xl:text-center col-span-12 xl:col-span-6 max-sm:flex'>
			<p className='text-2xl xl:text-4xl font-bold xl:font-semibold'>
				{count.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ' ') + prefix}
			</p>
			<p className='max-sm:ml-2 mt-1 xl:mt-2 text-base xl:text-xl text-gray-800 font-medium xl:font-semibold'>
				{title}
			</p>
		</div>
	)
}

export default Stat
