const TrustedBy = () => {
	const [windowSize, setWindowSize] = useState(getWindowSize())
	useEffect(() => {
		function handleWindowResize() {
			setWindowSize(getWindowSize())
		}

		window.addEventListener('resize', handleWindowResize)

		return () => {
			window.removeEventListener('resize', handleWindowResize)
		}
	}, [])
	
	const companyList2 = companyList.map(company => (
		<Company key={company.company} img={company.img} alt={company.name} />
	))
	const companyMobileList = companyList.map(company => (
		<CompanyMobile key={company.company} img={company.img} alt={company.name} />
	))


	return (
		<div className='xl:mx-10'>
			<div>
				<p className='font-semibold text-base xl:text-2xl text-center'>Pracowaliśmy z takimi firmami jak:</p>
			</div>
			<div className='grid grid-cols-6 gap-4 mt-6'>
				{windowSize.innerWidth > 768 ? (
					companyList2
				) : (
					<div className='h-114 m-auto overflow-hidden relative'>
						<div className='flex w-full animate-scroll'>{companyMobileList}</div>
					</div>
				)}
			</div>
		</div>
	)
}

export default TrustedBy
