import { Outlet } from 'react-router-dom'
import { useLocation } from 'react-router-dom'

import Navigation from '../components/navbarComponent/Navigation'
import MainCmp from '../components/mainComponent/Main'
import Footer from '../components/footerComponent/Footer'
import HeroSection from '../components/heroComponent/HeroSection'
import Hero from '../components/heroComponent/Hero'

const Root = () => {
	const location = useLocation()

	return (
		<div className='text-green-900 relative flex flex-col min-h-screen'>
			<Navigation />
			<main
				id='maincmp'
				className='bg-custom-color flex-1'>
				<MainCmp>
					{(location.pathname === '/ZZiR' || location.pathname === '/ZZiR/ZieloneDachy') && (
						<HeroSection children={<Hero />} />
					)}
					<Outlet />
				</MainCmp>
			</main>
			<Footer />
		</div>
	)
}

export default Root
